body {
    margin: 0;
    background-color: #9EEBCF;
}
.app {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
}
.card-container {
    height: 450px;
    width: 900px;
    margin: auto;
    display: block
}
.title {
    margin: 30px auto 0 auto;
    text-align: center;
    padding-top: 2%
}
::-webkit-scrollbar
{
  width: 10px;
  height: 10px; 
}

::-webkit-scrollbar-track
{
  background: rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb
{
  background: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}